const mapsheetData = (sheetData) => {
  if (!Array.isArray(sheetData) || sheetData.length === 0) {
    return { items: [] };
  }

  // Add debug logging
  console.log("Raw Sheet Data:", sheetData);

  const items = sheetData.map((row, index) => {
    // Debug log for each row
    console.log(`Processing row ${index}:`, row);
    
    const item = {
      type: row["Physical product/Raw Material"]?.toLowerCase() === "physical product"
        ? "VPMReference"
        : "Raw_Material",
      title: row["Title"] || "",
      attributes: {
        description: row["Description"] || "",
        ISV_ItemCatalogCategory: row["Item Catalog Category"] || "",
        "dseno:EnterpriseAttributes": {
          "XP_VPMReference_Ext.EMR_ERP_PrimaryUOM": row["Unit Of Measure"] || row["Unit of Measure"] || "Each"
        },
        "dseng:EnterpriseReference": {
          partNumber: row["EIN Number"] || ""
        }
      },
      classificationType: row["Type"] || "",
      collabspace: row["Collaborative Space"] || row["Collabspace"] || ""
    };

    // Debug log the created item
    console.log(`Mapped item ${index}:`, item);
    return item;
  });

  const result = { items };
  // Debug log final result
  console.log("Final mapped data:", result);
  return result;
};

export default mapsheetData;
